import dynamic from "next/dynamic";

const ChessBoardClient = dynamic(() => import("./ChessBoardClient"), { ssr: false });

export default function Page() {
  return (
    <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
      <h1 style={{ marginTop: 20, fontSize: 28 }}>♟️ Mi Chess Online</h1>
      <ChessBoardClient />
    </div>
  );
}
